@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.section-wrapper {
  padding: 2vh;
}

.bg-dark-grey {
  background-color: #222831;
}
.bg-grey {
  background-color: #222a30;
}

.section-title{
  font-size: 3rem;
  color: #ECECEC;
  text-align: center;
  padding: 0vh 0vh 1vh 0vh;
}

.sub-title {
  font-size: 2rem;
  color: #f2a365;
  text-align: center;
  padding-bottom: 2vh;
}
.sub-sub-title {
  font-size: 1rem;
  text-align: center;
  color: #ECECEC;
}

a {
  color: #f2a365;
}

.content {
  z-index: 2;
  position: relative;
}

.parallax-bg {
  height: 40vh;
  width: 100%;
  background-image: url(/static/media/parallax-bg.84244983.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;

  position: fixed;
  bottom: 0vh;
}

.container {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    text-align: center;
    color: white;
}

.into-name {
    font-size: 4rem;
}

.into-title {
    font-size: 2rem;
}

.flexitem-scroll-down {
    -webkit-align-self: center;
            align-self: center;
}

