.footer-container {
    width: 100%;
}
.footer-content {
    font-size: small;
    height: 5vh;
    color:white;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.contact-container {
    margin-bottom: 34vh;
}

.icon-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.contact-flex-item {
    padding: 2vh;
}

.contact-flex-item :hover {
    color: #f2a365;
}
.about-container {
    width: 100%;
    color: #ECECEC;
}

.about-wrapper {
    display: -webkit-flex;
    display: flex;
    margin: 5vh 5vw 5vh 5vw;
    -webkit-justify-content: center;
            justify-content: center;
}

.about-wrapper img {
    max-width: 20vw;
    max-height: auto;
}

.about-text {
    width: 40vw;
}

.about-text p{
    margin-bottom: 2vh;
    padding-right: 2vw;
}

/* This applies from 0px to 600px */
@media (max-width: 600px) {
    .about-wrapper {
        display: grid;
    }
    .about-text {
        width: 90vw;
    }
    .about-wrapper img {
        max-width: 90vw;
        max-height: auto;
    }
}

.project-grid {
    padding: 5vh 5vw 5vh 5vw;
    display: grid;
    grid-gap: 1rem;
    gap: 1rem;
  
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-auto-rows: 300px;
}

.grid-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    position: relative;
	object-fit: cover;
	width: 100%;
	height: 100%;

    color: #ECECEC;
}

.grid-item-overlay {
    background-color: #222831;
    opacity: 0;
    color: white;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
    -webkit-align-items: center;
            align-items: center;

    position: absolute;
    top: 0;
    bottom: 0px;
    left: 0;
    right: 0;
}

.grid-item-overlay:hover {
    opacity: 0.8;
}

.grid-item:hover .grid-item-title {
    visibility: hidden;
}

.grid-item-title {
    font-size: 2rem;
}

.overlay-description {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
}

.overlay-title {
    color: #f2a365;
    font-size: 1.5rem;
}

.overlay-description p {
    padding: 0 1vw 0 1vw;
    text-align: center;
}

